.Home {
  text-align: center;
}

.Home-logo {
  -webkit-animation: Home-logo-spin infinite 20s linear;
          animation: Home-logo-spin infinite 20s linear;
  height: 80px;
}

.Home-header {
  background: -webkit-gradient(linear, left top, left bottom, from(#555097), color-stop(31.11%, #725CA8), color-stop(64.44%, #A672C8), to(#C680DB));
  background: -webkit-linear-gradient(top, #555097 0%, #725CA8 31.11%, #A672C8 64.44%, #C680DB 100%);
  background: -o-linear-gradient(top, #555097 0%, #725CA8 31.11%, #A672C8 64.44%, #C680DB 100%);
  background: linear-gradient(180deg, #555097 0%, #725CA8 31.11%, #A672C8 64.44%, #C680DB 100%);
  padding: 20px;
  color: white;

}

.Home-button {
  background: #56ccf2;
  border:0px;
  height: 70px;
  width: 300px;
  font-size: 24px;
  font-weight: 500;
  border-radius: 8px;
  margin: 40px 24px 24px 24px;
  outline: none;
  -webkit-transition: .8s;
  -o-transition: .8s;
  transition: .8s;
  -webkit-box-shadow: 0 30px 60px rgba(0, 0, 0, 0.25);
          box-shadow: 0 30px 60px rgba(0, 0, 0, 0.25);

}

.Home-button:hover {
  background: white;
  -webkit-box-shadow: 0 40px 80px rgba(0, 0, 0, 0.5);
          box-shadow: 0 40px 80px rgba(0, 0, 0, 0.5);
  -webkit-transform: translateY(-2px) scale(1.1,1.1);
      -ms-transform: translateY(-2px) scale(1.1,1.1);
          transform: translateY(-2px) scale(1.1,1.1);
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.Home-title {
  font-size: 64px;
  -webkit-animation: fade-in 3s forwards;
          animation: fade-in 3s forwards;
  opacity: 0;
}

.Home-intro {
  font-size: 20px;
  text-align: left;

}

@-webkit-keyframes Home-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Home-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Home-grid {
  display: grid;
  grid-template-columns: 300px auto;
  padding: 24px;
  grid-gap: 24px;
  
}

.Home-card {
  /* width: 300px; */
  height: 200px;
  /* width:  */
  background:  #555097;
  border-radius: 9px;
  background-image: -webkit-linear-gradient(270.03deg, #31A6B5 0.03%, #52BDC5 34.28%, #53EEE4 70.98%, #4DF6EC 110.13%);
  background-image: -o-linear-gradient(270.03deg, #31A6B5 0.03%, #52BDC5 34.28%, #53EEE4 70.98%, #4DF6EC 110.13%);
  background-image: linear-gradient(179.97deg, #31A6B5 0.03%, #52BDC5 34.28%, #53EEE4 70.98%, #4DF6EC 110.13%);
}

.Home-gallery {
  display: grid;
  grid-gap: 20px;
  padding: 20px;
  grid-template-columns: repeat(3, 1fr);
  /* width: 640px; */
  margin: 0 auto;

}

@media(max-width: 640px) {
  .Home-title {
    
  }
}


@media(max-width: 650px) {
  .Home-gallery {
    /* width: 420px; */
  }
}


